@font-face {
  font-family: 'Roboto';
  src: url('./fonts/Roboto-Regular.ttf') format('truetype');
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: 'Roboto', sans-serif;
  max-width: 960px;
  margin: 1rem auto;
}
h1 {
  margin-bottom: 1rem;
  text-align: center;
  color: darkblue;
}
ul {
  list-style: none;
  li {
    padding: 1rem 2rem;
    border: 1px solid #ccc;
    border-radius: 10px;
    margin-bottom: 1rem;
  }
}

button {
  padding: 1rem 1.5rem;
  border: none;
}
